        tr {
            transition: background-color 0.25s ease;
            &:hover {
                background-color: $background_color_3;
            }
        }
    }
}

table.striped {
    display: table;
    border-collapse: collapse;
    border-spacing: 0;
    margin: 0 auto;
    width: 100%;
    thead {
        th {
            text-align: center;
            padding: 5px;
            div {
                margin-top: 9px;
            }
        }
        border-bottom: 1px solid black;
        th:first-child {
            width: 5%;
        }
        th:nth-child(2) {
            text-align: left;
            width: 60%;
        }
        th:nth-child(3) {
            text-align: right;
            width: 20%;
        }
        th:nth-child(4) {
            width: 15%;
        }
    }
    tbody {
        font-size: 0.76em;
        tr:nth-child(odd) {
            background-color: $table-striped-color;
            &:hover {
                background-color: $background_color_3;
            }
        }
        td {
            text-align: center;
            padding-left: 2px;
            padding-right: 2px;
        }
        td:first-child {
            padding: 5px 5px;
            width: 5%;
            border-right: 1px solid background_color_3;
        }
        td:nth-child(2) {
            text-align: left;
            width: 60%;
            border-right: 1px solid background_color_3;
        }
        td:nth-child(3) {
            text-align: right;
            width: 20%;
            border-right: 1px solid background_color_3;
        }
        td:nth-child(4) {
            width: 15%;
        }
    }
}

tbody.audit {
    font-size: 0.76em;
    tr:nth-child(odd) {
        background-color: $table-striped-color;
        &:hover {
            background-color: $background_color_3;
        }
    }
    td {
        text-align: center;
        padding-left: 2px;
        padding-right: 2px;
        width: 5% !important;
    }
    td:first-child {
        padding: 5px 5px;
        width: 5% !important;
        border-right: 1px solid background_color_3;
    }
    td:nth-child(2) {
        text-align: left;
