    }
    to {
        top: var(--snackbarCurrentTop);
        opacity: 1;
    }
}

@-webkit-keyframes fadeout {
    from {
        top: var(--snackbarCurrentTop);
        opacity: 1;
    }
    to {
        top: var(--snackbarTop);
        opacity: 0;
    }
}

@keyframes fadeout {
