        opacity: 1;
    }
    to {
        top: var(--snackbarTop);
        opacity: 0;
    }
}


/* new components */


/* CheckBox */

$md-checkbox-margin: 16px 0;
$md-checkbox-checked-color: $background_color_4;
$md-checkbox-border-color: rgba(0,
0,
0,
0.54);
$md-checkbox-border-color-disabled: rgba(0,
0,
0,
0.26);
$md-checkbox-checked-color-disabled: rgba(0,
0,
0,
0.26);
$md-checkbox-size: 9px;
$md-checkbox-padding: 4px;
$md-checkmark-width: 2px;
$md-checkmark-color: #fff;
.md-checkbox {
    position: relative;
    text-align: left;
    margin-left: 10px;
    &.md-checkbox-inline {
        display: inline-block;
    }
    label {
        cursor: pointer;
        &:before,
        &:after {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
        }
        &:before {
            // box
            width: $md-checkbox-size;
            height: $md-checkbox-size;
            background: #fff;
            border: 2px solid $md-checkbox-border-color;
            border-radius: 2px;
            cursor: pointer;
            transition: background 0.3s;
        }
        &:after {
            // checkmark
        }
    }
    input[type="checkbox"] {
        outline: 0;
        margin-right: $md-checkbox-size - 10px;
        visibility: hidden;
        &:checked {
            +label:before {
                background: $md-checkbox-checked-color;
                border: 2px solid $md-checkbox-checked-color;
            }
            +label:after {
                top: 2.35px;
                left: 3.35px;
                width: 4.78px;
                height: 4px;
                $md-checkmark-size: $md-checkbox-size - (2 * $md-checkbox-padding);
                transform: rotate(-45deg);
                /* top: ($md-checkbox-size / 2) - ($md-checkmark-size / 4) -
          $md-checkbox-size/10; */
                // left: $md-checkbox-padding;
                /* width: $md-checkmark-size;
        height: $md-checkmark-size / 2; */
                border: $md-checkmark-width solid $md-checkmark-color;
                border-top-style: none;
                border-right-style: none;
            }
        }
        &:disabled {
            +label:before {
                border-color: $md-checkbox-border-color-disabled;
            }
            &:checked {
                +label:before {
                    background: $md-checkbox-checked-color-disabled;
                }
            }
        }
    }
}

