    width: 680px;
    height: 240px;
    background-color: #fff;
    position: absolute;
    top: 80px;
    z-index: 12001;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    border-radius: 3px;
    box-shadow: 0 16px 28px 0 rgba(0, 0, 0, 0.22), 0 25px 55px 0 rgba(0, 0, 0, 0.21);
    cursor: default;
}

.md-select {
    display: inline-block;
    font-family: inherit;
    background-color: transparent;
    width: 150px;
    padding-left: 5px;
    font-size: 1em;
    color: rgba(0, 0, 0, 0.26);
    border: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}

#user-List {
    position: absolute;
    background-color: #f6f6f6;
    min-width: 230px;
    border: 1px solid #ddd;
    max-height: 200px;
    overflow-y: auto;
    margin-top: 60px;
    margin-left: 65px;
    z-index: 1;
}

#user-List a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

#user-List a:hover {
    background-color: #5da407;
}

.hide {
    display: none;
}

@media only screen and (max-width: 760px) {
    .head-container {
        flex-direction: column;
        justify-content: center;
        height: auto;
        text-align: center;
    }
    #hamburger-button {
        display: block !important;
        color: $color_2;
        text-align: left;
        padding: 8px;
        &:hover {
            cursor: pointer !important;
        }
    }
    .footer-unify {
        position: relative;
    }
    .status {
        text-align: center;
    }
    .nav-unify {
        display: none;
        // top: 0;
        // height: auto;
        // flex-direction: column;
        // position: relative;
        // background: #293037;
        // text-align: center;
    }
    .nav-unify a,
    #settings {
        text-align: center;
        margin-top: 3px;
        margin-bottom: 3px;
    }
}

@media only screen and (mim-zoom: 100) {
    .footer-unify {
        height: 70px;
        width: 100vw;
        background-color: $background_color_1;
    }
}

.separator {
    height: 2px;
    border-bottom: 1px solid $color_2;
    display: block;
    &:hover {
        background-color: $background_color_1;
    }
}

.viewlog {
    width: 80%;
    max-height: 60vh;
}

.logFail {
    color: red;
}

.logInfo {
    color: green;
}

.logWarning {
    color: blue;
}

i.expirated {
    color: red;
    &:hover {
        cursor: not-allowed;
    }
}