.ModalDialog-mask {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
}

.ModalDialog-input {
  font-size: 0.9em;
  margin-left: auto;
  margin-right: auto;
  margin-top: 25px;
  padding: 10px 10px 5px 5px;
  -webkit-appearance: none;
  display: block;
  background: #fafafa;
  color: #636363;
  width: 280px;
  border: none;
  border-radius: 0;
  border-bottom: 1px solid #757575;
}

.ModalDialog-close {
  position: absolute;
  top: 12px;
  right: 12px;
  display: block;
  width: 14px;
  height: 14px;
  background: url(../img/modal_close.png);
  z-index: 2;
}

.ModalDialog-input:focus {
  outline: none;
}

.ModalDialog-label {
  color: #999;
  font-size: 0.9em;
  font-weight: normal;
  position: relative;
  pointer-events: none;
  left: 45px;
  top: -20px;
  transition: all 0.2s ease;
}
/* active */
.input-field {
  margin: 0;
}

.input-field label .active {
  display: block;
}

.ModalDialog-input:focus ~ .ModalDialog-label,
.ModalDialog-input.used ~ .ModalDialog-label {
  position: relative;
  transform: scale(0.75);
  left: 45px;
  top: -50px;
  /* font-size: 14px; */
  color: #4a89dc;
}

.ModalDialog-body {
  position: fixed;
  z-index: 9999;
  width: 420px;
  max-width: 720px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: white;
  border-radius: 6px;
  -webkit-box-shadow: 0 0 48px rgba(0, 0, 0, 0.15);
  box-shadow: 0 0 48px rgba(0, 0, 0, 0.15);
  -webkit-animation: modal-dialog-show 0.1s;
  animation: modal-dialog-show 0.1s;
}
.ModalDialog-body.download {
  width: auto;
  max-width: 980px;
  min-width: 400px;
}
.ModalDialog-body.shareFile {
  width: 780px;
  max-width: 980px;
}

.btn-input [type="file"] {
  cursor: inherit;
  display: block;
  font-size: 999px;
  filter: alpha(opacity=0);
  min-height: 100%;
  min-width: 100%;
  opacity: 0;
  position: absolute;
  right: 0;
  text-align: right;
  line-height: 0.9em;
  top: 0;
  cursor: pointer;
}

.ModalDialog-input-field {
  position: relative;
  margin: 12px 0 12px;
}

@media screen and (max-width: 480px) {
  .modal-dialog-alert-message-body {
    min-width: 320px;
    max-width: 100%;
  }
}
.ModalDialog-title {
  background-color: lightgrey;
  font-weight: bold;
  font-size: 1.3em;
  padding: 8px 10px;
  border-radius: 6px 6px 0 0;
}
.ModalDialog-container {
  widows: 100%;
}

.ModalDialog-content {
  padding: 10px 10px 5px 10px;
  font-size: 1em;
}
.ModalDialog-button {
  position: relative;
  padding: 15px;
  font-size: 1em;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ModalDialog-button a {
  display: block;
  margin: 0 24px;
  color: #3cc51f;
  text-decoration: none;
  border-radius: 3px;
  padding: 2px 24px;
}
a.ModalDialog-button-cancel,
a.ModalDialog-button-cancel:link {
  color: #ffffff;
  background-color: #95a5a6;
}
a.ModalDialog-button-cancel:hover {
  color: #ffffff;
  background-color: #7f8c8d;
  text-decoration: none;
}

a.ModalDialog-button-confirm,
a.ModalDialog-button-confirm:link {
  color: #ffffff;
  background-color: #34495e;
}
a.ModalDialog-button-confirm:hover {
  color: #ffffff;
  background-color: #2c3e50;
  text-decoration: none;
}

@-webkit-keyframes modal-dialog-show {
  0% {
    -webkit-filter: opacity(0.2);
    filter: opacity(0.2);
    margin-top: -16px;
  }
  100% {
    -webkit-filter: opacity(1);
    filter: opacity(1);
    margin-top: 0;
  }
}
@keyframes modal-dialog-show {
  0% {
    -webkit-filter: opacity(0.2);
    filter: opacity(0.2);
    margin-top: -16px;
  }
  100% {
    -webkit-filter: opacity(1);
    filter: opacity(1);
    margin-top: 0;
  }
}

.modal-dialog-open {
  overflow: hidden;
}

.warning-lbl {
  color: red;
  margin: 0;
  padding: 0;
  font-size: 1.3em;
  text-align: center;
  font-weight: bold;
}

.warning-msg {
  margin: 10px auto;
  color: red;
  font-weight: 700;
  font-size: 1.2em;
  text-align: center;
  width: 315px;
}
.msg {
  text-align: center;
  font-weight: bold;
}
