$color_1: #fff;
$font_family_1: sans-serif;
$background_color_1: #28a8e0;
$background_color_2: #1b8aba;
$border_color_1: #aaa;
.list {
    font-family: $font_family_1;
}

.user-row {
    display: flex;
    justify-content: space-between;
    flex: 1 1 1;
    border: 1px solid #ccc;
    div {
        width: 100%;
        padding: 5px;
        border-right: 1px solid #ccc;
        &:nth-child(2)::first-letter {
            text-transform: uppercase;
        }
    }
}

.head-list {
    display: flex;
    justify-content: space-between;
    flex: 1 1 1 1 0 1;
    border: 1px solid #ccc;
    div {
        width: 100%;
        padding: 5px;
        border-right: 1px solid #ccc;
    }
}


