.dataTable-container {
    max-height: 60vh;
    overflow-y: scroll;
}

.dataTable-bottom>div:last-child,
.dataTable-top>div:last-child {
    text-align: center;
    //float: right;
}

.dataTable-bottom::after,
.dataTable-top::after {
    clear: both;
    content: " ";
    display: table;
}

.dataTable-bottom,
.dataTable-top {
    padding: 8px 10px;
}

.dataTable-wrapper {
    position: relative;
}

.head-Title {
    margin: 18px auto;
    text-align: center;
    font-size: 1.2em;
    font-weight: bold;
}

.tableList {
    margin: 20px auto;
    border-collapse: collapse;
    max-width: 790px;
    th {
        background-color: #ccc;
        padding: 3px 8px;
        border-bottom: 1px solid rgb(151, 150, 150);
        border-left: 1px solid white;
        border-right: 1px solid white;
        width: 200px;
        font-weight: bold;
        font-size: .8em;
        &:last-child {
            padding: 1px;
            width: 10%;
        }
    }
}

#bodyList {
    td {
        padding: 5px 10px;
        //border: 1px solid #ccc;
        width: 200px;
        font-size: .8em;
        &:last-child {
            border-right: 1px solid rgb(151, 150, 150);
            max-width: 300px !important;
            padding: 1px;
        }
        &:first-child {
            border-left: 1px solid rgb(151, 150, 150);
        }
    }
    tr:nth-child(even) {
        background-color: rgb(248, 248, 248);
    }
    tr:hover {
        background-color: #cfe9c8;
    }
    tr:last-child {
        border-bottom: 1px solid rgb(151, 150, 150);
    }
}

.del-user-icon,
.edit-user-icon {
    margin-left: 3px;
    margin-right: 3px;
    color: var(--main-color);
}

.del-user-icon:hover,
.edit-user-icon:hover {
    cursor: pointer;
}